import { useLocation, useNavigate } from 'react-router-dom';
import 'react-datepicker/dist/react-datepicker.css';
import { useState } from 'react';
import 'assets/css/batteriesAddOrUpdate.css';
import 'assets/css/otaAddOrUpdate.css';

function AddOrUpdate() {
  const [startDate, setStartDate] = useState(new Date());
  const navigate = useNavigate();
  const location = useLocation();
  const param = location.state;
  const historyBack = () => {
    navigate(-1);
  };

  return (
    <div className="section__grid__wrap content batteries__add__update ota__add_update">
      {/* sub_nav */}
      <div className="sub__nav">
        <div className="left">
          <button onClick={historyBack}>
            <svg viewBox="0 0 24 24" stroke="white">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M11 17l-5-5m0 0l5-5m-5 5h12"
              />
            </svg>
          </button>
          <dl>
            <dt className="f__medium">OTA 패키지 관리 → {param.flag}</dt>
          </dl>
        </div>
        <div className="right btn__wrap">
          <button className="custom__btn">
            <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M12 6v6m0 0v6m0-6h6m-6 0H6"
              />
            </svg>
            <span className="f__medium">삭제</span>
          </button>
          <button className="custom__btn">
            <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M12 6v6m0 0v6m0-6h6m-6 0H6"
              />
            </svg>
            <span className="f__medium">저장</span>
          </button>
        </div>
      </div>
      {/* // sub_nav */}
      {/* input__wrap */}
      <section className="input__wrap">
        <div className="section__inner">
          <ul>
            <li>
              <dl>
                <dt>버전 표기법</dt>
                <dd>
                  <select className="custom__select">
                    <option>전체</option>
                  </select>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>버전 선택</dt>
                <dd className="version__input">
                  <input type="number" className="custom__input" />
                  <input type="number" className="custom__input" />
                  <input type="number" className="custom__input" />
                  <input type="number" className="custom__input" />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>OTA파일</dt>
                <dd>
                  <input type="text" className="custom__input" />
                  <button className="custom__btn">
                    <span className="f__medium">선택</span>
                  </button>
                </dd>
              </dl>
            </li>
            <li>
              <textarea
                className="custom__input"
                readOnly
                placeholder="여기에 파일을 끌어서 올려주세요"
              />
            </li>
            <li>
              <dl>
                <dt>배포이유</dt>
                <dd>
                  <input type="text" className="custom__input" />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>변경내역</dt>
                <dd>
                  <textarea className="custom__input" />
                </dd>
              </dl>
            </li>
          </ul>
        </div>
      </section>
      {/* // input__wrap */}
    </div>
  );
}

export default AddOrUpdate;
